label.cluevo {
  display: inline-flex;
  flex-direction: column;
  font-size: .9em;

  input {
    width: auto;
  }
}

.cluevo-admin-notice, .cluevo-notice {
  box-shadow: $box-shadow;
  padding: 1px 12px;
  border-left: 4px solid $cluevo-light-gray;
  margin: 10px 0 10px 0;
  background: #FFFFFF;
  color: $cluevo-gray;

  &.cluevo-is-dismissible {
    position: relative;

    .cluevo-notice-dismiss {
      position: absolute;
      top: 0;
      right: 1px;
      border: none;
      margin: 0;
      padding: 9px;
      background: 0 0;
      color: #72777c;
      cursor: pointer;

      &:before {
        background: 0 0;
        color: #72777c;
        content: '\f153';
        display: block;
        font: 400 16px/20px dashicons;
        speak: none;
        height: 20px;
        text-align: center;
        width: 20px;
        -webkit-font-smoothing: antialiased;
      }
    }
  }

  &.cluevo-notice-success, &.cluevo-notice-notice {
    border-left: 4px solid $cluevo-teal;
  }

  &.cluevo-notice-warning {
    border-left: 4px solid $cluevo-orange;
  }

  &.cluevo-notice-error {
    border-left: 4px solid $cluevo-pink;
  }

  &.cluevo-notice-info {
    border-left: 4px solid $cluevo-gray;

    &.cluevo-secondary {
      border-left: 4px solid $cluevo-light-gray;
    }
  }

  &.cluevo-is-dismissable {
    padding-right: 38px;
    position: relative;
  }

  .cluevo-notice-title {
    font-weight: bold;
    margin: 10px 0;
    color: $cluevo-gray;
  }
}

.cluevo-spinner {
  width: 100%;
  display: flex;
  justify-content: center;

  .cluevo-spinner-message {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .cluevo-spinner-segment {
    width: 32px;
    height: 32px;
    display: inline-block;
    margin: 10px;
    position: relative;

    &:after {
      position: absolute;
      content: '';
      width: 32px;
      height: 32px;
      animation: cluevo-spinner 1s infinite;
    }

    &.cluevo-spinner-segment-pink {
      background-color: $cluevo-pink;

      &:after {
        background: $cluevo-pink;
      }
    }

    &.cluevo-spinner-segment-purple {
      background-color: $cluevo-purple;

      &:after {
        background: $cluevo-purple;
        animation-delay: .33s;
      }
    }

    &.cluevo-spinner-segment-teal {
      background-color: $cluevo-teal;

      &:after {
        background-color: $cluevo-teal;
        animation-delay: .66s;
      }
    }
  }
}

@keyframes cluevo-spinner {
  0% {
    opacity: 1;
    transform: scale(1); // rotate(0);
    //border-radius: 0;
  }
  100% {
    opacity: 0;
    transform: scale(1.6); // rotate(180deg);
    //border-radius: 50%;
  }
}

.cluevo-progress-container {
  width: 100%;
  height: 2px;
  display: flex;
  background: $cluevo-pink;
  background: $progress-background;

  .cluevo-progress {
    display: inline-block;
    margin-left: auto;
    background: lightblue;
    height: inherit;
    max-width: 100%;
    position: relative;
    overflow: hidden;
    background: $progress-bar-background;
    width: 0%;
    animation: progress-bar 1s;

    &:after {
      position: absolute;
      right: 0;
      font-size: 0.7rem;
      content: attr(data-pct);
      margin: 0 5px 0 0;
      opacity: 0;
      color: $progress-color;
      transition: opacity 0.3s ease-in-out;
    }
  }

  &.indeterminate {
    background-size: 400% 400%;
    animation: progress-bar-indeterminate 10s ease infinite;
  }

  &:hover {
    .cluevo-progress {
      &:after {
        opacity: 1;
      }
    }
  }
}

@keyframes progress-bar {
  0% {
    width: 100%;
  }
}

@keyframes progress-bar-indeterminate { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
