.cluevo-btn, input[type="button"].cluevo-btn, input[type="submit"].cluevo-btn, a.cluevo-btn, button.cluevo-btn {
  box-shadow: $box-shadow;
  padding: 4px 10px;
  margin: 0 4px;
  min-height: 20px;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: $cluevo-gray;
  color: #FFFFFF;
  transition: all .3s ease-in-out;
  border: none;
  text-decoration: none;
  position: relative;
  cursor: pointer;

  &.cluevo-btn-square {
    width: 24px;
    height: 24px;

    img {
      width: auto;
      height: auto;
    }
  }

  &:not(.cluevo-btn-mini) {
    &:after {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 10px 0 0;
      border-color: $cluevo-light-gray transparent transparent transparent;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
    }

    &:before {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 10px 0 0;
      border-color: $cluevo-light-gray transparent transparent transparent;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: scale(1);
      opacity: 1;
      z-index: 2;
      transition: all 0.5s ease-in;
    }

    &:hover {
      &:before {
        border-width: 15px 15px 0 0;
      }
    }

    &.cluevo-btn-primary {
      &:after {
        border-top-color: $cluevo-pink;
      }
      &:before {
        border-top-color: $cluevo-pink;
      }
    }

    &.cluevo-btn-secondary {
      &:after {
        border-top-color: $cluevo-purple;
      }
      &:before {
        border-top-color: $cluevo-purple;
      }
    }

    &.disabled, &[disabled] {
      background-color: $cluevo-light-gray;
      cursor: default;

      &:after, &:before {
        display: none;
      }
    }
  }

}
