@import '_variables.scss';
@import '_cluevo.scss';
@import '_common.scss';

.cluevo-lms-metadata {
  padding: 10px;
}

.cluevo-content-area-container {
  display: flex;
  justify-content: center;

  @media all and (max-width: $break-point-small) {
    .entry-header {
      display: flex;
      justify-content: space-around;
    }
  }

  .cluevo.content-area {
    flex: 1 0;
    max-width: 95%;
    max-width: 1500px;
  }
}

.cluevo-module-iframe {
  width: 100%;
  min-height: 500px;
  border: none;
  padding: 0;
  margin: 0;
}

.cluevo-scorm-module-iframe-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 0;

  label {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    select {
      width: auto;
    }
  }
}

.cluevo-completed {
  .progress {
    background: #d1097a; /* Old browsers */
    background: -moz-linear-gradient(
      left,
      #d1097a 0%,
      #3ce9e8 100%
    ); /* FF3.6-15 */
    background: -webkit-linear-gradient(
      left,
      #d1097a 0%,
      #3ce9e8 100%
    ); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to right,
      #d1097a 0%,
      #3ce9e8 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(
      startColorstr='#d1097a',
      endColorstr='#3ce9e8',
      GradientType=1
    ); /* IE6-9 */
  }
}

@keyframes wiggle {
  0% {
    transform: rotate(0);
  }
  33% {
    transform: rotate(10deg);
  }
  66% {
    transform: rotate(-10deg);
  }
}

.cluevo-learner-name {
  display: flex;
  justify-content: space-between;
  align-items: baseline;

  h1 {
    margin-bottom: 0;
  }
}

.cluevo-user-widget {
  position: relative;

  #cluevo-polygraph {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    box-shadow: $box-shadow;
    z-index: 3;
    width: 100%;

    svg {
      width: 100%;
    }
  }

  .cluevo-level-container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;

    p {
      margin: 0;
    }

    .cluevo-level {
      font-size: 2.5rem;
      line-height: 2.4rem;
      margin-right: 5px;
    }

    .cluevo-exp-bar-container {
      font-size: 0.7rem;
      display: flex;
      flex-direction: column;
      flex: 1 1 100%;

      .cluevo-exp-values-container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 2px;

        .cluevo-level-pct {
          display: inline-block;
          margin-left: 5px;
        }
      }

      .cluevo-exp-bar {
        .cluevo-progress-container {
          min-width: 200px;
          height: 1.3em;
          background: $cluevo-pink;
          background: $progress-background;
        }
      }

      .cluevo-exp-sub-container {
        display: flex;
        justify-content: space-between;

        .cluevo-title {
          font-size: 1rem;
          text-align: right;
        }

        .cluevo-competences {
          cursor: pointer;
        }
      }
    }
  }
}

.cluevo-content-list {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  flex-wrap: wrap;
  max-width: 1500px;

  .cluevo-notice {
    width: 100%;
  }

  @media all and (max-width: $break-point-small) {
    justify-content: space-around;
    .cluevo-content {
      flex-basis: 95%;
      padding: 0;
      margin-bottom: 20px;
    }
  }

  @media all and (min-width: $break-point-small) and (max-width: $break-point-medium) {
    .cluevo-content {
      flex-basis: 50%;
      //padding: 10px;

      //&:nth-child(2n + 1) {
      //padding-left: 0px;
      //}
    }
  }

  @media all and (min-width: $break-point-medium) and (max-width: $break-point-large) {
    .cluevo-content {
      //flex-basis: 33.33%;
      //padding: 5px;

      //&:nth-child(2n + 1) {
      //padding-left: 5px;
      //}
      //&:nth-child(3n + 1) {
      //padding-left: 0;
      //}
    }
  }

  @media all and (min-width: $break-point-large) {
    .cluevo-content {
      //flex-basis: 25%;
      //padding: 10px;
    }
  }

}

.cluevo-content {
  width: 350px;
  margin: 0 10px 10px 0;

  &.draft {
    opacity: 0.5;
  }

  //@media all and (min-width: $break-point-large) {
  //padding: 10px;

  //&:nth-child(4n + 1) {
  //padding-left: 0;
  //}
  //}

  //&:first-child {
  //padding-left: 0;
  //}


  .cluevo-content-item-link {
    display: flex;
    flex-direction: column;
    //height: 300px;
    background: #FFFFFF;
    box-shadow: $box-shadow;
    color: $cluevo-gray;
    text-decoration: none !important;
    outline: none;
    transition: box-shadow .3s ease-in-out;

    &.cluevo.tree {
      .cluevo-meta-bg {
        svg {
          .shine {
            fill: $cluevo-pink;
          }
        }
      }
    }

    &.cluevo-course {
      //border-top: 2px solid $cluevo-pink;
      .cluevo-meta-bg {
        svg {
          .shine {
            fill: $cluevo-pink;
          }
        }
      }
    }

    &.cluevo-chapter {
      //border-top: 2px solid $cluevo-purple;
      .cluevo-meta-bg {
        svg {
          .shine {
            fill: $cluevo-purple;
          }
        }
      }
    }

    &.cluevo-module {
      //border-top: 2px solid $cluevo-teal;
      .cluevo-meta-bg {
        svg {
          .shine {
            fill: $cluevo-teal;
          }
        }
      }
    }

    &:hover, &:focus {
      text-decoration: none;
      box-shadow: $box-shadow-hover;
      color: $cluevo-gray;
      .cluevo-post-thumb {
        .cluevo-item-type-corner {
          border-width: 48px 48px 0 0;
        }
      }
    }

    &:visited, &:active, &:focus {
      color: $cluevo-gray;
    }

    .cluevo-post-thumb {
      position: relative;
      height: 200px;
      overflow: hidden;

      .cluevo-item-type-corner {
        width: 0;
        height: 0;
        position: absolute;
        top: 0;
        left: 0;
        border-style: solid;
        border-width: 32px 32px 0 0;
        border-color: #FFFFFF transparent transparent transparent;
        transition: all 0.3s ease-in-out;

        &.course {
          border-color: $cluevo-pink transparent transparent transparent;
        }

        &.chapter {
          border-color: $cluevo-purple transparent transparent transparent;
        }

        &.module {
          border-color: $cluevo-teal transparent transparent transparent;
        }
      }

      .cluevo-item-type-corner-bottom {
        width: 0;
        height: 0;
        position: absolute;
        bottom: 0;
        right: 0;
        border-style: solid;
        border-width: 0 0 32px 32px;
        border-color: transparent transparent #ffffff transparent;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top;
        transform: scale(1) translate(0);
        transition: transform 1s ease-in-out;
      }

      .cluevo-meta-bg {
        position: absolute;
        width: 100%;
        height: 170px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        top: 0;

        .meta-bg-corner {
          background: #ffffff;
          width: 102%;
          height: 100px;
          transform: rotate(-5deg) skew(-5deg);
          position: absolute;
          bottom: -80px;
          left: -1%;
        }

        svg {
          width: 100%;
          height: auto;
          .shine {
            opacity: .4;
            transition: all .5s ease-in-out;
          }
        }

        img {
          width: 100%;
          border-radius: 0;
        }
      }

      .cluevo-meta-container {
        position: absolute;
        height: 30px;
        bottom: 0;
        width: 100%;
        z-index: 1;
        display: flex;
        flex-direction: row-reverse;
        padding: 0 5px;
        align-items: flex-start;
        color: $cluevo-gray;
        background: #FFFFFF;
        box-sizing: border-box;

        div {
          margin-left: 5px;
          color: $cluevo-gray;

          &.cluevo-alert {
            animation: wiggle 0.3s infinite;
          }

          &.cluevo-item-type {
            margin-left: 0;
            margin-right: auto;
            color: $cluevo-light-gray;
            font-size: .9rem;
          }
        }
      }
    }

    .cluevo-description {
      padding: 5px;
      color: $cluevo-gray;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .cluevo-excerpt {
      display: none;
    }
  }

  &:hover {
    .cluevo-content-item-link {
      .cluevo-post-thumb {
        > img {
          transform: scale(1.3) translate(5%, -5%);
          transition: transform 10s ease-in-out;
        }
        .cluevo-meta-bg {
          .shine {
            //animation: tile-shine 5s ease-in-out infinite alternate;
            opacity: 1;
          }
        }
      }
    }
  }
}

.cluevo-content-list-style-row {
  flex-direction: column;

  .cluevo-content {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 10px;
    height: 50px;

    .cluevo-content-item-link {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      position: relative;

      &:hover {
        .cluevo-post-thumb {
          .cluevo-item-type-corner {
            border-width: 24px 24px 0 0;
          }
        }
      }

      .cluevo-content-container {
        flex: 1;
      }

      .cluevo-post-thumb {
        height: 50px;
        display: flex;
        flex-direction: row;
        position: static;

        .cluevo-item-type-corner {
          border-width: 16px 16px 0 0;
        }

        img {
          max-width: 70px;
          border-radius: 0;
        }

        .cluevo-meta-container {
          right: 2px;
          top: 2px;
          width: auto;
          align-items: center;
          background: transparent;
          box-sizing: border-box;

          > div {
            margin: 0 2px;

            &.cluevo-item-type {
              display: none;
            }
          }
        }

        .cluevo-meta-bg {
          display: none;
          height: 10px;
          top: auto;
          bottom: 100%;

          svg, img {
            display: none;
          }
        }
      }

      .cluevo-description {
        padding: 0 0 0 5px;
      }

      .cluevo-excerpt {
        display: block;
        padding: 0 40px 0 5px;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 20px;
        font-size: 0.8em;
      }

      .cluevo-description {
        font-weight: bold;
      }
    }

  }
}


#cluevo-login-form {
  box-shadow: $box-shadow;
  width: 300px;
  padding: 30px;
  margin: 0 auto;

  label {
    display: block;
    font-size: 0.8rem;
    color: #72777c;
  }
}

.cluevo-user-competences-container {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;

  .cluevo-competence-container {

    a.cluevo-content-item-link {
      overflow: hidden;
      position: relative;

      p {
        margin: 0;
      }

      .cluevo-post-thumb {
        img {
          transition: filter 0.3s ease-in-out;
          border-radius: 0;
        }
        img.blur {
          filter: blur(2px);
        }

        .cluevo-meta-bg {
          z-index: 2;
        }

        .cluevo-meta-container {
          z-index: 2;
        }

      }
    }

    .cluevo-content-container {
      background: #FFFFFF;
      z-index: 1;
    }

    .cluevo-competence-modules {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 100%;
      left: 0;
      transition: all 0.3s ease-in-out;
      //background: rgba(255, 255, 255, .85);
      background: rgba(80, 80, 80, 0.5);
      width: 100%;
      height: 100%;

      ul {
        padding: 5%;
        margin: 0;
        list-style-type: none;
        color: #ffffff;

        li {
          margin-bottom: 10px;
          font-size: 12px;

          .cluevo-competence-module-progress-container {
            display: flex;
            justify-content: space-between;

            p {
              margin: 0;
              padding: 0;
              max-width: 90%;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;

              &.cluevo-comp-module-name {
                flex: 1;
              }
            }
          }
        }
      }

      &.cluevo-active {
        top: 0;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

#cluevo-polygraph {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;

  &.cluevo-active {
    opacity: 1;
    visibility: visible;
  }

  polygon {
    fill: $cluevo-gray;
    //stroke: $cluevo-purple;
    //opacity: .75;
  }

  circle {
    fill: $cluevo-light-gray;
    stroke: $cluevo-purple;
    stroke-width: 4px;
  }

  text {
    font-family: Helvetica Neue, Arial, sans-serif;
    font-size: 10px;
    fill: $cluevo-pink;
  }

  label {
    display: inline-block;
    font-size: 14px;
  }

  .cluevo-polygraph-stats-container {
    padding: 20px 10px;

    .cluevo-labels {
      display: flex;
      justify-content: space-between;

      label {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        &.cluevo-comp-name {
          flex: 1;
        }
      }
    }
    .cluevo-progress-container {
      height: 2px;
    }
  }
}

.cluevo-badge {
  &.cluevo-module-type {
    width: 24px;
    img {
      width: 100% !important;
      height: auto !important;
      box-shadow: none !important;
      border: none !important;
    }
  }
}

.cluevo-module-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 50px;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  min-height: 500px;

  iframe {
    width: 100%;
    flex: 1 1 auto;
    border: none;
  }
}

#cluevo-module-lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: block;
  background: rgba(#FFFFFF, 0.9);
  z-index: 1000000;
  display: none;
  overflow: auto;
  -webkit-overflow-scrolling: touch;

  .cluevo-error {
    display: flex;
    text-align: center;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    color: $cluevo-pink;
    font-weight: bold;
    height: 100%;

    .cluevo-error-close-button {
      font-weight: normal;
      margin-top: 20px;
    }
  }

  &.cluevo-media {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
  }

  video {
    max-width: 100%;
    max-height: 100%;
  }

  div.cluevo-spinner-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  div.cluevo-close-button {
    min-width: 24px;
    min-height: 24px;
    width: fit-content;
    cursor: pointer;
    box-shadow: $box-shadow;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 1.5em;
    background: $cluevo-light-gray;
    color: #FFFFFF;
    transition: all .3s ease-in-out;

    &:hover {
      background: $cluevo-gray;
      color: $cluevo-pink;
    }
  }

  &.no-close-button {
    div.cluevo-close-button {
      display: none;
    }
  }

  &[data-lightbox-close-button-position="top-left"] {
    div.cluevo-close-button {
      top: 0;
      left: 0;
    }
  }
  &[data-lightbox-close-button-position="top-center"] {
    div.cluevo-close-button {
      top: 0;
      left: calc(50% - 24px);
    }
  }
  &[data-lightbox-close-button-position="top-right"] {
    div.cluevo-close-button {
      top: 0;
      right: 0;
    }
  }
  &[data-lightbox-close-button-position="bottom-left"] {
    div.cluevo-close-button {
      top: unset;
      bottom: 0;
      left: 0;
    }
  }
  &[data-lightbox-close-button-position="bottom-center"] {
    div.cluevo-close-button {
      top: unset;
      bottom: 0;
      left: calc(50% - 24px);
    }
  }
  &[data-lightbox-close-button-position="bottom-right"] {
    div.cluevo-close-button {
      top: unset;
      bottom: 0;
      right: 0;
    }
  }

  iframe {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
    border: none !important;
    overflow: auto;
  }
}

.cluevo-module-tile-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 30px);
  width: 100%;
  flex-direction: column;
  overflow-y: auto;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.8);
  z-index: 1;
  display: none;

  h2 {
    margin-top: 10px;
    font-size: 12px;
    text-align: center;
  }

  .cluevo-prompt-btns-container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
.cluevo-content-list {
  &.cluevo-content-list-style-row {
    .cluevo-module-tile-overlay {
      height: 100%;
      overflow: visible;
    }
    .cluevo-sco-select-container {
      height: 100%;
      overflow: visible;
    }
  }
}

.cluevo-sco-select-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 30px);
  width: 100%;
  flex-direction: column;
  overflow-y: auto;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.8);
  z-index: 1;
  display: none;

  h2 {
    font-size: 16px;
    text-align: center;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {

      a {
        display: block;
        background: transparent;
        transition: all 0.2s ease-in-out;
        padding: 1px;
        text-align: center;
        text-decoration: none !important;
        color: $cluevo-pink;
        &:hover {
          background: $cluevo-gray;
          color: $cluevo-teal;
        }
      }
    }
  }
}

iframe#cluevo-module-iframe {
  width: 100%;
  min-height: 500px;
  height: 100vh;
  border: none;
}

body, html {
  &.cluevo-module-overlay-active {
    overflow: hidden;
    max-height: 100vh;
  }
}

.cluevo-back-link-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;

  @media all and (max-width: $break-point-small) {
    display: flex;
    justify-content: space-around;
  }
  .cluevo-tree-parent-link {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
  }

  .cluevo-content-list-style-switch {
    margin-left: auto;

    .cluevo-btn {
      background: $cluevo-light-gray;
      color: $cluevo-gray;

      &.active, &:hover {
        background: $cluevo-gray;
        color: $cluevo-light-gray;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.cluevo-module-nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  a.cluevo-module-link, a.cluevo-module-link:hover {
    text-decoration: none;
    box-shadow: none;
    transition: none;

    &.cluevo-module-link-next {
      margin-left: auto;
    }
    &.cluevo-module-link-prev {
      margin-right: auto;
    }
  }
}

.cluevo-crumb-container {
  display: flex;
  flex-direction: row;
  align-items: center;

  .cluevo-crumb-spacer {
    margin: 0 5px;
  }

  .cluevo-crumb {
    position: relative;
    overflow: visible;

    p {
      margin-bottom: 5px;
    }

    p.cluevo-crumb-title {
      a {
        text-decoration: none;
      }
    }

    ul.cluevo-crumb-children {
      padding: 10px;
      display: none;
      position: absolute;
      z-index: 100;
      opacity: 0;
      transition: opacity 1s ease-in-out;
      background: rgba(0, 0, 0, 0.7);
      list-style-type: none;
      min-width: 100%;
      margin: 0;

      li {
        white-space: nowrap;

        &:hover {
        }

        a {
          color: #ffffff;
          text-decoration: none;
        }
      }
    }

    &:hover {
      ul {
        display: block;
        opacity: 1;
      }
    }
  }
}

.cluevo-alert-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  z-index: 100000;
  background: rgba(255, 255, 255, 0.8);

  .cluevo-alert {
    position: relative;
    background: #fff;
    min-width: 600px;
    max-width: 1000px;
    z-index: 100000;
    box-shadow: $box-shadow;
    padding: 10px 20px 20px 20px;

    @media all and (max-width: 779px) {
      min-width: auto;
      max-width: 100%;
    }

    .cluevo-alert-title {
      text-transform: uppercase;
      margin-bottom: 5px;
      font-weight: bold;
    }

    .cluevo-alert-close-button {
      width: 32px;
      height: 32px;
      position: absolute;
      top: 5px;
      right: 5px;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.cluevo-type-corner {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  border-style: solid;
  border-width: 32px 32px 0 0;
  border-color: #FFFFFF transparent transparent transparent;
  transition: all 0.3s ease-in-out;

  &.error {
    border-color: $cluevo-pink transparent transparent transparent;
  }

  &.warning {
    border-color: $cluevo-purple transparent transparent transparent;
  }

  &.info {
    border-color: $cluevo-teal transparent transparent transparent;
  }
}

@keyframes tile-shine {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
